import { withRouter } from 'next/router'

const ActiveLinkPpal = ({ children, router, href }) => {
    
  const ruta = router.pathname.split("-");
  const rutahref = href.split("-");
  const className = ruta[2] === rutahref[2] ? 'active' : 'inactive'
  const handleClick = e => {
    e.preventDefault()
    router.push(href)
  }
  return (
    <a href={href} onClick={handleClick} className={className}>
      {children}
    </a>
  )
}

export default withRouter(ActiveLinkPpal)